}

@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
  .preloader {
    background-image: url("../img/preloader-bg@3x.png");
  }
}

@media only screen and (min-width: 400px) {
  .preloader {
    background-size: 380px;
  }
}

.preloader-header {
  background-color: #FF7E09;
  padding-top: 25px;
  padding-bottom: 10px;
}

.logo__img {
  font-size: 0;
}

.logo__img img {
  width: 450px;
  width: calc(349px + (450 - 349) * ((100vw - 375px) / (768 - 375)));
}

@media (max-width: 375px) {
  .logo__img img {
    width: 349px;
  }
}

@media (min-width: 768px) {
  .logo__img img {
    width: 450px;
  }
}

.preloader-title {
  font-size: 26px;
  font-size: calc(17px + (26 - 17) * ((100vw - 320px) / (768 - 320)));
  margin-bottom: 1.2em;
}

@media (max-width: 320px) {
  .preloader-title {
    font-size: 17px;
  }
}

@media (min-width: 768px) {
  .preloader-title {
    font-size: 26px;
  }
}

.progress {
  display: flex;
}

.progress__line {
  flex: 1;
  height: 19px;
  background-color: #C4C4C4;
  border-radius: 6px;
  position: relative;
  margin-bottom: 25px;
}

.progress__count {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #FF7E09;
  border-radius: 6px;
  max-width: 100%;
