}

@media (min-width: 768px) {
  .load-box__text {
    font-size: 18px;
  }
}

.preloader-img {
  font-size: 0;
}

.preloader-img img {
  height: 260px;
  height: calc(196px + (260 - 196) * ((100vw - 375px) / (768 - 375)));
  transform-origin: bottom center;
  animation: wobble;
  animation-timing-function: ease-in;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@media (max-width: 375px) {
  .preloader-img img {
    height: 196px;
  }
}

@media (min-width: 768px) {
  .preloader-img img {
    height: 260px;
  }
}

@keyframes wobble {
  0% {
    transform: translateZ(0);
  }
  5% {
    transform: translate3d(-15%, 0, 0) rotate(-5deg);
  }
  10% {
    transform: translate3d(10%, 0, 0) rotate(3deg);
  }
  15% {
    transform: translate3d(-5%, 0, 0) rotate(-3deg);
  }
  20% {
    transform: translate3d(3%, 0, 0) rotate(2deg);
  }
  25% {
    transform: translate3d(-3%, 0, 0) rotate(-1deg);
  }
  to {
    transform: translateZ(0);
  }
}

.btn {
  font-family: "marvin";
  align-items: center;
  display: inline-block;
  text-align: center;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 12px 30px;
  font-size: 12px;
  line-height: 1.5;
  border-radius: 2px;
  text-decoration: none;
  transition: .3s;
}

.btn--block {
