.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  outline: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal-header {
  position: relative;
}

.modal-header .close {
  padding: 8px;
  box-sizing: content-box;
  position: absolute;
  top: -36px;
  right: -36px;
  cursor: pointer;
}

.modal-header .close__icon {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  border: 3px solid #FF7E09;
  display: block;
}

.modal-header .close__icon:before, .modal-header .close__icon:after {
  position: absolute;
  content: '';
  border-radius: 50%;
  width: 16px;
  height: 3px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-color: #FF7E09;
}

.modal-header .close__icon:before {
  transform: rotate(45deg);
}

.modal-header .close__icon:after {
  transform: rotate(-45deg);
}

.modal-dialog {
  margin: auto;
  max-width: 500px;
  display: flex;
  align-items: center;
  min-height: calc(100% - (.5rem * 2));
}

.modal-content {
  background: rgba(248, 243, 239, 0.98);
  border-radius: 5px;
  margin: 32px;
}

.modal-body {
  padding-left: 60px;
  padding-right: 60px;
  padding-bottom: 40px;
}

@media only screen and (max-width: 380px) {
  .modal-body {
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 20px;
  }
}

.modal-form .modal-content {
  background-image: url("../img/enot-form.png"), url("../img/modal-form-2.png");
  background-repeat: no-repeat;
  background-position: 100% 100%, -30px 50%;
}

@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
  .modal-form .modal-content {
    background-image: url("../img/enot-form@2x.png"), url("../img/modal-form-2.png");
    background-size: 117px;
  }
}

@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
  .modal-form .modal-content {
    background-image: url("../img/enot-form@3x.png"), url("../img/modal-form-2.png");
    background-size: 117px;
  }
}

.modal-form .modal-header {
  position: relative;
  background-color: #FF7E09;
  padding: 16px 0;
  margin-bottom: 20px;
}

.modal-form .modal-header:before {
  position: absolute;
  content: '';
  background-color: #FF7E09;
  filter: blur(4px);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.modal-form .modal-header:after {
  position: absolute;
  content: '';
  width: 96px;
  height: 96px;
  background-image: url("../img/modal-form-1.png");
  background-repeat: no-repeat;
  right: 15%;
  top: -10px;
}

.modal-form .modal-title {
  font-size: 14px;
  letter-spacing: 0.045em;
  color: #fff;
  position: relative;
  z-index: 10;
}

.modal-action .modal-content {
