.alert {
  visibility: hidden;
  overflow: hidden;
  transition: opacity .3s;
  opacity: 0;
  padding: 24px;
  margin: auto;
  position: fixed;
  left: 30px;
  right: 30px;
  top: 30px;
  bottom: 30px;
  z-index: 100;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.alert:before {
  position: absolute;
  content: '';
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  opacity: .7;
  border-radius: 8px;
  border: 1px solid #FF7E09;
  background-color: #fff;
}

.alert__img {
  text-align: center;
  position: relative;
  display: flex;
  align-items: flex-start;
  margin: auto;
}

.alert__img .phone {
  max-width: 200px;
  position: relative;
  z-index: 10;
}

@media only screen and (max-height: 460px) {
  .alert__img .phone {
    max-width: 150px;
  }
}

@media only screen and (max-height: 380px) {
  .alert__img .phone {
    max-width: 100px;
  }
}

.alert__img .phone-title {
  max-width: 300px;
  position: relative;
  z-index: 10;
}

@media only screen and (max-height: 460px) {
  .alert__img .phone-title {
    max-width: 200px;
  }
}

@media only screen and (max-height: 380px) {
  .alert__img .phone-title {
    max-width: 150px;
  }
}

.alert__img .phone-2 {
  z-index: 5;
  position: absolute;
  left: 0;
  bottom: 0;
  transform: rotate(90deg);
  transform-origin: 50% 75%;
  animation: back;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes back {
  0% {
    transform: rotate(90);
  }
  25% {
    transform: rotate(90);
  }
  50% {
    transform: rotate(0);
  }
  to {
    transform: rotate(0);
  }
}

.alert-backdrop {
  visibility: hidden;
  transition: opacity .6s;
  opacity: 0;
  z-index: 99;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.carousel:hover .carousel__bg {
  box-shadow: 0px 0px 20px #FF0303;
  filter: blur(10px);
}

@media all and (orientation: landscape) {
  .alert,
  .alert-backdrop {
    opacity: 1;
    visibility: visible;
  }
}
