@keyframes wobble {
	0% {
		transform: translateZ(0); }
	5% {
		transform: translate3d(-15%,0,0) rotate(-5deg); }
	10% {
		transform: translate3d(10%,0,0) rotate(3deg); }
	15% {
		transform: translate3d(-5%,0,0) rotate(-3deg); }
	20% {
		transform: translate3d(3%,0,0) rotate(2deg); }
	25% {
		transform: translate3d(-3%,0,0) rotate(-1deg); }
	to {
		transform: translateZ(0); } }


.btn {
	font-family: "marvin";
	align-items: center;
	display: inline-block;
	text-align: center;
	// white-space: nowrap
	user-select: none;
	background-color: transparent;
	border: 1px solid transparent;
	padding: 12px 30px;
	font-size: 12px;
	line-height: 1.5;
	border-radius: 2px;
	text-decoration: none;
	transition: .3s;
	&--block {
		width: 100%; }
	&--block + .btn--block {
		margin-top: 24px; }
	&--primary {
		background-color: $primary;
		border-color: $primary;
		color: #fff; }
	&--primary:not(:disabled):active {
		box-shadow: 0px 0px 27px #FF8318, 0px 0px 20px #FF7E09; } }

.form-input {
	font-family: 'Roboto', sans-serif;
	display: block;
	width: 100%;
	font-size: 12px;
	border-radius: 2px;
	border: 0.5px solid $primary;
	padding: 10px 16px;
	height: 46px;
	background-color: transparent;
	@include placeholder {
		font-size: 12px;
		color: #C4C4C4;
		font-family: 'Roboto', sans-serif; } }

@mixin on-circle($item-count, $circle-size, $item-size, $translate-factor: 2.6) {
	position: absolute;
	width:  $circle-size;
	height: $circle-size;
	padding: 0;
	border-radius: 50%;
	list-style: none;
	> * {
		display: block;
		position: absolute;
		top:  50%;
		left: 50%;
		transition: .3s;
		width:  $item-size;
		height: $item-size;
		margin: -(($item-size + 2) / 2);
		$toCenter: 0;
		$angle: (360 / $item-count);
		$rot: -90;
		@for $i from 1 through $item-count {
			&:nth-of-type(#{$i}) {
				transform: rotate($rot * 1deg) translate($circle-size / $translate-factor) rotate((90) * 1deg); }
			$rot: $rot + $angle; } } }


