	&__img-3 {
		font-size: 0;
		position: absolute;
		left: -20px;
		bottom: 0;
		img {
			@include css-lock(127, 200, 320, 768, width); } }
	&:before {
		z-index: -2;
		position: absolute;
		content: '';
		background: rgba(196, 196, 196, 0.15);
		right: 15%;
		bottom: -30%;
