.main-content {
	padding: 40px 25px;
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	@include r(380) {
		padding: 20px 15px;
		padding-top: 40px; } }

.spin {
	margin-bottom: 15px;
	flex: 2; }

.spin-container {
	position: relative;
	max-width: 260px;
	margin: auto;
	margin-bottom: 20px;
	@include rmin(380) {
		max-width: 300px; }
	@include rmin(480) {
		max-width: 400px; }
	@include rmin(580) {
		max-width: 500px; }
	@include rh(680) {
		max-width: 260px; }
	&__btn {
		position: absolute;
		position: absolute;
		right: -45px;
		top: -35px;
		img {
			@include css-lock(153, 230, 375, 768, width);
			transform-origin: bottom left;
			animation-delay: 10s;
			animation: wobble-2;
			animation-timing-function: ease-out;
			animation-duration: 4s;
			animation-iteration-count: infinite;
			animation-direction: alternate-reverse;
			@include rh(680) {
				width: 153px; } } } }

@keyframes wobble-2 {
	0% {
		transform: translateZ(0); }
	5% {
		transform: translate3d(-5%,0,0) rotate(-15deg); }
	10% {
		transform: translate3d(5%,0,0) rotate(13deg); }
	15% {
		transform: translate3d(-3%,0,0) rotate(-13deg); }
	20% {
			transform: translate3d(3%,0,0) rotate(12deg); }
	25% {
		transform: translate3d(-1%,0,0) rotate(-10deg); }
	to {
		transform: translateZ(0); } }

.carousel {
	margin: auto;
	padding-bottom: 100%;
	border-radius: 50%;
	position: relative;
	&__bg svg {
		border-radius: 50%;
		width: 100%;
		height: 100%; }
	&__bg {
		border-radius: 50%;
		position: absolute;
		width: 260px;
		height: 260px;
		left: 0;
		top: 0;
		transition: .3s;
		@include rmin(380) {
			width: 300px;
			height: 300px; }
		@include rmin(480) {
			width: 400px;
			height: 400px; }
		@include rmin(580) {
			width: 500px;
			height: 500px; }
		@include rh(680) {
			width: 260px;
			height: 260px; } }
	&__center {
		width: 78px;
		height: 78px;
		border-radius: 50%;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto; }
	&__gift {
		left: 0;
		top: 0;
		@include on-circle(10, 260px, 40px, 2.5);
		@include rmin(380) {
			@include on-circle(10, 300px, 70px); }
		@include rmin(480) {
			@include on-circle(10, 400px, 70px); }
		@include rmin(580) {
			@include on-circle(10, 500px, 90px); }
		@include rh(680) {
			@include on-circle(10, 260px, 40px); } }
	&__start {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		font-size: 20px;
		color: $primary;
		background-color: #fff;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		border: 4px solid #3ED7FB;
		transition: .3s;
		z-index: 10; }
	&__start:hover {
		cursor: pointer; }
	&__start:active {
		text-shadow: 0px 0px 9px #FD8A26, 0px 0px 20px #FD8A26; }
	&__arrow {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 62px;
		margin: auto;
		width: 24px;
		height: 62px;
		transform-origin: center bottom; } }



