.modal {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 1050;
	display: none;
	overflow: hidden;
	outline: 0;
	background-color: rgba(#000, 0.7); }

.modal-header {
	position: relative;
	.close {
		padding: 8px;
		box-sizing: content-box;
		position: absolute;
		top: -36px;
		right: -36px;
		cursor: pointer; }
	.close__icon {
		height: 24px;
		width: 24px;
		border-radius: 50%;
		border: 3px solid $primary;
		display: block;
		&:before,
		&:after {
			position: absolute;
			content: '';
			border-radius: 50%;
			width: 16px;
			height: 3px;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto;
			background-color: $primary; }
		&:before {
			transform: rotate(45deg); }
		&:after {
			transform: rotate(-45deg); } } }


.modal-dialog {
	margin: auto;
	max-width: 500px;
	display: flex;
	align-items: center;
	min-height: calc(100% - (.5rem * 2)); }

.modal-content {
	background: rgba(248, 243, 239, 0.98);
